<script>
import { useAccountStore, useFileStore, useTemplateStore } from '@/store'
import { mapActions, mapState, mapWritableState } from 'pinia'

import debug from 'debug'

import TemplateCanvas from '@/components/TemplateEditor/components/Canvas/Canvas.vue'
import MessageModal from '@/components/TemplateEditor/modals/MessageModal.vue'
import { capture } from '@/plugins/snapshot'
import { GET_TEMPLATE, ISSUE_USER_CREDENTIAL, UPLOAD_FILE } from '@/store/actions'

// const KEYVAL = 'keyval'
// const TABLE = 'table'

const logDebug = debug('IssueCredential')

export default {
  name: 'IssueCredential',
  title: 'pageTitle.issueCredential',
  components: {
    MessageModal,
    TemplateCanvas,
  },
  emits: ['close'],
  data() {
    return {
      // Data to delete
      tableValues: [],
      tableLine: [],
      tables: [],

      // Currently used
      /** template */
      currentLayout: '',

      tid: '',
      attributesMenu: {},

      step: 0,
      isLoaded: false,
      imageLoading: false,

      email: null,
      errorMail: null,

      reload: true,
      sending: false,

      templateValuesFront: [],
      templateValuesBack: [],

      buttonText: this.$t('button.next'),
      showSuccessModal: false,
      pages: ['#page'],

      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.[^\n\r@\u2028\u2029]*@.+\..+/.test(v) || this.$t('issue.emailError'),
      ],
      issueCredentialWidth: 0,
    }
  },
  computed: {
    ...mapState(useAccountStore, ['adminEmail', 'cid', 'caName']),
    ...mapState(useTemplateStore, ['template']),
    ...mapWritableState(useTemplateStore, ['components', 'backSide']),
  },
  watch: {},
  // eslint-disable-next-line vue/no-deprecated-destroyed-lifecycle
  beforeDestroy() {
    logDebug('beforeUnmount() tid: ', this.tid)
    window.onbeforeunload = null
  },
  created() {
    const tid = this.$route.params?.tid
    this.tid = tid
    useAccountStore()[GET_TEMPLATE]({ tid }).then((data) => {
      logDebug('GET_TEMPLATE response', data)
      this.initialLoad(data)
    })
  },
  mounted() {
    window.onbeforeunload = function () {
      return 'Are you sure you want to close the window?'
    }
  },
  methods: {
    ...mapActions(useFileStore, { uploadFile: UPLOAD_FILE }),
    uploadImage(field, index) {
      logDebug('uploadImage function', field, index)
      logDebug(this.$refs)
      this.$refs.uploadImage[index].click()
    },
    async newImage(event, field) {
      this.imageLoading = true
      field.value = await this.uploadFile({
        file: event.target.files[0],
        folder: `${this.tid}`,
      })
        .catch((err) => {
          console.error(err)
          throw err
        })
      this.imageLoading = false

      this.replaceAttributeImage(field)
    },
    async initialLoad(template = {}) {
      logDebug('initialLoad', template)
      const frontendProps = template.frontend_props || {}
      const templateItens = template.templateItens || []
      if (templateItens.length === 0) {
        this.isLoaded = true
        return
      }

      useTemplateStore().setTemplate(template)
      // const components = frontendProps.components || []

      logDebug('this.components: ', this.components)
      this.createTemplateItems(templateItens)

      this.backSide = false

      if (frontendProps.backgroundBack) {
        if (!this.pages.includes('#back') && this.pages.length === 1) {
          this.pages.push('#back')
        }

        this.backSide = true
      }

      await this.splitBaseTextIntoArray(this.components)

      useTemplateStore().setActiveComponent()
      // this.components = components
      // TODO: Check if this is necessary
      await this.$forceUpdate()

      logDebug('issueCredentialWidth', this.$refs.formTemplate, this.$refs.formTemplate.offsetWidth, this.$refs.formTemplate.clientWidth, this.$refs.formTemplate.scrollWidth, this.$refs.formTemplate.scrollWidth - this.$refs.formTemplate.clientWidth)
      this.issueCredentialWidth = this.$refs.formTemplate.clientWidth - 300
      this.isLoaded = true
    },
    async splitBaseTextIntoArray(components) {
      logDebug('splitBaseTextIntoArray', components)
      components.forEach(async (component) => {
        if (component.templateSide === 'back') {
          if (this.pages.length === 1) {
            this.pages.push('#back')
          }

          this.backSide = false
        }
        if (
          component.type === 'text'
          || (component.type === 'image'
          && component.dynamicImage)
        ) {
          component.baseText = component.text.match(
            // eslint-disable-next-line regexp/no-obscure-range
            /(\[[A-Z0-9_]+\])|([a-zA-Z0-9!@#$&()\\-`.+,/"]*)/g,
          )
          component.originalText = component.text.match(
            // eslint-disable-next-line regexp/no-obscure-range
            /(\[[A-Z0-9_]+\])|([a-zA-Z0-9!@#$&()\\-`.+,/"]*)/g,
          )
        }
        if (component.type === 'image') {
          logDebug('components[i]', component)
          component.src = await this.toDataURL(
            component.src,
          )
        }
      })

      this.templateValuesFront.forEach((item) => {
        // logDebug(item);
        item.indexArray = this.findAttr(item, components)
      })
      this.templateValuesBack.forEach((item) => {
        // logDebug(item);

        item.indexArray = this.findAttr(item, components)
      })
      this.templateValuesFront.sort(this.compareAttr)
      this.templateValuesBack.sort(this.compareAttr)

      logDebug('templateValuesFront', this.templateValuesFront)
      logDebug('templateValuesBack', this.templateValuesBack)
    },

    compareAttr(a, b) {
      if (a.attr < b.attr) {
        return -1
      }
      if (a.attr > b.attr) {
        return 1
      }
      return 0
      // return a.attr.localeCompare(b.attr);
    },

    findAttr(item, components) {
      let indexAttr, indexComponent

      components.some((component, i) => {
        if (
          (component.type === 'text'
          && component.baseText
          && component.baseText.length > 0)
          || (component.type === 'image'
          && component.dynamicImage)
        ) {
          indexAttr = component.baseText.findIndex(el => el.includes(`[${item.attr}]`))
          // logDebug(indexAttr);
          if (indexAttr > -1) {
            indexComponent = i
            return true
          }
          return false
        }
        return false
      })
      return { indexComponent, indexAttr }
    },
    replaceAttributeImage(component) {
      logDebug('replaceAttributeImage')
      logDebug(component)
      const indexComponent = component.indexArray.indexComponent

      this.components[indexComponent].src = component.value

      // TODO: Check if this is necessary
      this.$forceUpdate()
    },
    replaceAttributeText(component) {
      logDebug('replaceAttributeText')
      logDebug(component)
      const indexAttr = component.indexArray.indexAttr
      const indexComponent = component.indexArray.indexComponent

      logDebug(this.components[indexComponent])
      // Get Base text
      const baseText = this.components[indexComponent].baseText

      if (component.value) {
        baseText[indexAttr] = component.value
        logDebug(baseText)
      } else {
        baseText[indexAttr]
            = this.components[indexComponent].originalText[indexAttr]
      }
      if (this.components[indexComponent].maxCharacters) {
        component.maxCharacters
            = this.components[indexComponent].maxCharacters
      }

      const newValue = baseText.join(' ')
      // logDebug(newValue);
      this.components[indexComponent].text = newValue

      // TODO: Check if this is necessary
      this.$forceUpdate()
    },

    createTemplateItems(templateItens) {
      logDebug('createTemplateItems: ')
      templateItens.forEach((e) => {
        const templateValue = {
          attr: e.attr,
          value: null,
          error: false,
          type: e.type,
          temp_item_id: e._id,
          isPublic: e.isPublic,
          isMandatory: e.isMandatory,
        }
        if (e.order === 0) {
          this.templateValuesFront.push(templateValue)
        } else if (e.order === 1) {
          this.templateValuesBack.push(templateValue)
        }
      })
    },

    async toDataURL(url) {
      logDebug('toDataURL', url)
      if (url?.startsWith('data:image')) {
        return url
      }

      return await new Promise((resolve) => {
        const xhr = new XMLHttpRequest()
        xhr.onload = function () {
          const reader = new FileReader()
          reader.onloadend = function () {
            resolve(reader.result)
          }
          reader.readAsDataURL(xhr.response)
        }
        xhr.onerror = function () {
          resolve(undefined)
          console.error('** An error occurred during the XMLHttpRequest')
        }
        xhr.open('GET', `${url}?_=${Date.now()}`, true)
        xhr.responseType = 'blob'
        xhr.send()
      })
    },
    clearError(index) {
      if (
        this.templateValuesFront[index]
        && this.templateValuesFront[index].value
      ) {
        this.templateValuesFront[index].error = false
      } else if (
        this.templateValuesBack[index]
        && this.templateValuesBack[index].value
      ) {
        this.templateValuesBack[index].error = false
      } else {
        this.templateValuesFront[index].error = true
      }
    },
    checkForm() {
      logDebug('Check form')
      let error = false
      this.templateValuesFront.forEach((e) => {
        if ((e.isMandatory === 'true' || e.isMandatory === true) && !e.value) {
          e.error = true
          error = true
        } else if (['DATEOFBIRTH', 'EXPIRYDATE'].includes(e.attr)) {
          const date = new Date(e.value)
          if (date.toString() === 'Invalid Date') {
            e.error = true
            error = true
          } else {
            e.error = false
          }
        } else {
          e.error = false
        }
      })
      this.templateValuesBack.forEach((e) => {
        if ((e.isMandatory === 'true' || e.isMandatory === true) && !e.value) {
          e.error = true
          error = true
        } else {
          e.error = false
        }
      })
      if (this.tables && this.tables.length > 0 && this.tableValues.length === 0) {
        error = true
      }

      return error
    },
    async createData() {
      let templateValuesFrontMapped = this.templateValuesFront.map(e =>
        ({
          value: e.value,
          temp_item_id: e.temp_item_id,
          isPublic: e.isPublic,
        }),
        // }
      )

      await Promise.all(templateValuesFrontMapped).then((results) => {
        templateValuesFrontMapped = results
      })
      let templateValuesBackMapped = this.templateValuesBack.map(e =>
        ({
          value: e.value,
          temp_item_id: e.temp_item_id,
          isPublic: e.isPublic,
        }),
        // }
      )

      await Promise.all(templateValuesBackMapped).then((results) => {
        templateValuesBackMapped = results
      })
      this.data = templateValuesFrontMapped.concat(templateValuesBackMapped)
      if (
        this.tables
        && this.tables.length > 0
        && this.tableValues.length > 0
      ) {
        this.data.push({
          values: this.tableValues,
          temp_item_id: this.tables[0].item_id,
          isPublic: true,
        })
      }
      logDebug('Generated Data: ', this.data)
    },
    close() {
      this.$emit('close')
    },
    back() {
      this.step = 0
      this.buttonText = this.$t('button.next')
      this.email = null
    },
    async takeTemplateSnapshot() {
      // TODO: Check if this is necessary
      this.reload = false
      await this.$nextTick()
      this.reload = true
      await this.$nextTick()

      const snapshotElementId = 'template-canvas'
      const element = document.getElementById(snapshotElementId)

      const capturedImage = await capture(element)
      logDebug('takeTemplateSnapshot:imgArray', capturedImage)
      const imgArray = await this.uploadFile({
        file: capturedImage,
        folder: `${this.cid}`,
      })
        .catch((err) => {
          console.error(err)
        })
      logDebug('UPLOAD_FILE:result', imgArray)

      return [imgArray]
    },
    async nextStep() {
      try {
        switch (this.step) {
          case 0:
            if (this.checkForm()) {
              this.sending = false
              return
            }
            await this.createData()
            this.step += 1
            this.sending = false
            // await this.takeTemplateSnapshot()

            break
          case 1:
            logDebug('Emit cert')
            this.sending = true
            this.errorMail = null

            if (!this.email) {
              this.errorMail = this.$t('issue.emailError')
              throw new Error('Email is required')
            }
            await useAccountStore()[ISSUE_USER_CREDENTIAL](
              {
                tid: this.tid,
                data: this.data,
                email: this.email,
                imageArray: await this.takeTemplateSnapshot(),
              },
            )
            this.showSuccessModal = true

            break

          default:
            break
        }
      } catch (error) {
        console.error(error)
        this.sending = false
      }
    },
  },

}
</script>

<template>
  <v-container class="issue-cred px-0">
    <v-row>
      <v-col cols="6" class="">
        <h2 class="title_header pr-4">
          {{ $t('issue.title') }}
        </h2>
        <div class="steps">
          {{ $t('step[0]') }} {{ step + 1 }} {{ $t('step[1]') }} 2
        </div>
      </v-col>
      <v-col cols="6" class="text-right pr-0">
        <v-btn v-if="step === 1" class="back mr-4" @click="back()">
          {{
            $t('button.back')
          }}
        </v-btn>
        <v-btn class="next" :loading="sending" @click="nextStep()">
          {{ buttonText }}
        </v-btn>
      </v-col>
    </v-row>
    <v-card flat class="pa-0 mt-6 issue-form-wrapper">
      <v-row>
        <v-col cols="12" md="12" lg="5" class="pt-0">
          <v-stepper v-model="step" elevation="0">
            <v-stepper-items>
              <v-stepper-content step="0" class="pa-3">
                <v-container class="" style="max-height: 55vh; overflow-y: auto">
                  <v-row>
                    <v-col
                      v-if="templateValuesBack.length > 0"
                      cols="12"
                      class=""
                    >
                      <p class="mb-0 side-header">
                        {{ $t('createCertModal.front') }}
                      </p>
                    </v-col>
                    <v-col
                      v-for="(field, index) in templateValuesFront"
                      :key="field._id"
                      cols="12"
                      class="input-field pb-0"
                    >
                      <label class="">
                        {{
                          field.attr === 'IMAGE' && field.type === 'image'
                            ? $t('issue.labelUpload')
                            : field.attr
                        }}
                        <span
                          v-if="field.isMandatory === 'true'"
                          style="color: #e95e5e"
                        >
                          *
                        </span>
                      </label>

                      <v-menu
                        v-if="field.inputType === 'date'"
                        v-model="attributesMenu[field.attr]"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                      >
                        <template #activator="{ on }">
                          <v-text-field
                            color="#009fb1"
                            readonly
                            :value="templateValuesFront[index].value"
                            v-on="on"
                          />
                        </template>
                        <v-date-picker
                          v-model="templateValuesFront[index].value"
                          :locale="$i18n.locale"
                          no-title
                          @input="
                            attributesMenu[field.attr] = false;
                            replaceAttributeText(field);
                          "
                        />
                      </v-menu>

                      <!-- text inputs -->
                      <v-text-field
                        v-else-if="field.type !== 'image'"
                        v-model="templateValuesFront[index].value"
                        class="mt-2"
                        :type="field.type"
                        flat
                        solo
                        :maxlength="field.maxCharacters"
                        :counter="field.maxCharacters"
                        :error="templateValuesFront[index].error"
                        @input="
                          clearError(index);
                          replaceAttributeText(field);
                        "
                      />
                      <v-btn
                        v-show="field.type === 'image'"
                        text
                        :loading="imageLoading"
                        class="advance-btn upload my-5"
                        @click="uploadImage(field, field.indexArray.indexAttr)"
                      >
                        {{ $t('issue.buttonUpload') }}
                      </v-btn>

                      <input
                        v-if="field.type === 'image'"
                        ref="uploadImage"
                        type="file"
                        style="display: none"
                        accept="image/*"
                        @change="newImage($event, field)"
                      />
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col
                      v-if="templateValuesBack.length > 0"
                      cols="12"
                      class=""
                    >
                      <p class="mb-0 side-header">
                        {{ $t('createCertModal.back') }}
                      </p>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col
                      v-for="(field, index) in templateValuesBack"
                      :key="field._id"
                      cols="12"
                      class="input-field pb-0"
                    >
                      <label class="">{{ field.attr }}

                        <span
                          v-if="field.isMandatory === 'true'"
                          style="color: #e95e5e"
                        >
                          *
                        </span>
                      </label>
                      <v-text-field
                        v-model="templateValuesBack[index].value"
                        class="mt-2"
                        :type="field.type"
                        flat
                        solo
                        :error="templateValuesBack[index].error"
                        @input="
                          clearError(index);
                          replaceAttributeText(field);
                        "
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-stepper-content>
              <v-stepper-content step="1" class="pa-3">
                <v-container class="">
                  <v-row>
                    <v-col
                      cols="12"
                      class="input-field pb-0"
                      style="padding-right: 10px"
                    >
                      <label class=""> {{ $t('issue.emailField') }}</label>
                      <v-text-field
                        v-model="email"
                        class="mt-2"
                        flat
                        persistent-hint
                        :rules="emailRules"
                        :hint="$t('issue.emailHint')"
                        :error-messages="errorMail"
                        solo
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
        </v-col>
        <v-col cols="12" lg="7" class="pt-0 template-background">
          <v-row>
            <v-col
              ref="formTemplate"
              cols="12"
              class="pt-6"
            >
              <TemplateCanvas
                v-if=" isLoaded && reload "
                :editable="false"
                :width="issueCredentialWidth"
                style="max-height: 60vh;"
              />

              <!-- create a div that is behind all the html and is full width and height -->
              <div
                style="position: absolute; top: 0; left: 0;  z-index: -1; opacity: 0; overflow: hidden;"
              >
                <TemplateCanvas
                  v-if=" isLoaded && reload"
                  :editable="false"
                  :width="1420"
                  :snapshot="true"
                />
              </div>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-card>
    <MessageModal
      v-if="showSuccessModal"
      message="email"
      @close="$router.go(-1)"
    />
  </v-container>
</template>

